<style scoped lang="less">

  #canvas{
    width: 90%!important;
    height: auto!important;
  }


</style>

<template>
  <div class="component-wrapper-class" style="overflow: scroll">

    <div style="width: 100%;">
      <h1></h1>
      <canvas id="canvas"></canvas>
    </div>


    <h1></h1>
    <img :src="cell.imgLink" style="width: 90%; "/>


    <h4>1007</h4>
    <input style="margin-top: 20px;" name="file" type="file" accept="image/*" @change="update"/>

<!--

    <form action="http://192.168.1.101:8081/qRCode/updateImgDomain" method="post" enctype="multipart/form-data">
      <input type="text" name="updateImgDomain">
      <input type="file" name="file" accept="image/*">
      <button type="submit"></button>
    </form>
-->



  </div>
</template>

<script>
  import TSCenter from '../utils/TempStorageCenter'

  import QRCode from 'qrcode'     //

  export default {
    name: 'contact',
    data(){
      return {
        cell:''
      }
    },
    mounted() {

      let _this = this;

      var canvas = document.getElementById('canvas')
      QRCode.toCanvas(canvas, `https://qi1216689986.github.io/rderweima?suid=${_this.cell.imgdomainId}`, function (error) {
        if (error) console.error(error)
        console.log('success!');


        var canvasImg = document.getElementsByTagName('canvas')[0];
        var img =_this.convertCanvasToImage(canvasImg);

      })

    },
    methods: {

      convertCanvasToImage(canvas) {
        //ImageDOM
        var image = new Image();
        // canvas.toDataURL Base64URL,
        // PNG
        image.src = canvas.toDataURL("image/png");
        return image;
      },

      update(e){
        let _this = this

        let file = e.target.files[0];
        let param = new FormData(); //form
        param.append('file',file,file.name);//appendform

        param.append("imgdomainId", _this.cell.imgdomainId);//appendform

        param.append('chunk','0');//form

        console.log("param.get('file')==>",param.get('file')); //FormDataget
        console.log('param==>', param);

        console.log('imgdomainId==>', param.get('imgdomainId'));

        let config = {

          headers:{'Content-Type':'multipart/form-data'}
        };  //


        this.$http.post('/qRCode/updateImgDomain',param,config)
          .then(response=>{
            console.log('', response.data);
          }).catch(function (error) {
          console.log('',error);
        })
      }
    },
    created() {
      let _this = this;
      _this.cell = TSCenter.sharedInstance().getGoodsWithKey('object')

      if (!_this.cell) {
        _this.$router ? this.$router.back() : window.history.back()
      }

      console.log('_this.cell',_this.cell);
    }
  }
</script>

